"use client";
import { useState, useEffect } from "react";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import {
  DollarSign,
  TrendingUp,
  CreditCard,
  Users,
  Package,
} from "lucide-react";

// Mock data for revenue charts
const mockRevenueData = {
  daily: [
    { date: "Mon", revenue: 1200 },
    { date: "Tue", revenue: 1800 },
    { date: "Wed", revenue: 1500 },
    { date: "Thu", revenue: 2100 },
    { date: "Fri", revenue: 2400 },
    { date: "Sat", revenue: 1900 },
    { date: "Sun", revenue: 1300 },
  ],
  weekly: [
    { date: "Week 1", revenue: 8500 },
    { date: "Week 2", revenue: 9200 },
    { date: "Week 3", revenue: 8900 },
    { date: "Week 4", revenue: 11200 },
  ],
  monthly: [
    { date: "Jan", revenue: 32000 },
    { date: "Feb", revenue: 28000 },
    { date: "Mar", revenue: 35000 },
    { date: "Apr", revenue: 42000 },
    { date: "May", revenue: 38000 },
    { date: "Jun", revenue: 45000 },
  ],
};

const mockTopProducts = [
  { id: 1, name: "Wool Blend Coat", sales: 42, revenue: 5459.58 },
  { id: 2, name: "Leather Ankle Boots", sales: 38, revenue: 5699.62 },
  { id: 3, name: "Cashmere Sweater", sales: 35, revenue: 3149.65 },
  { id: 4, name: "Silk Scarf", sales: 31, revenue: 1859.69 },
  { id: 5, name: "Winter Jacket", sales: 28, revenue: 4199.72 },
];

export default function RevenuePage() {
  const [period, setPeriod] = useState("weekly");
  const [loading, setLoading] = useState(true);
  const [revenueData, setRevenueData] = useState(mockRevenueData);
  const [topProducts, setTopProducts] = useState(mockTopProducts);

  useEffect(() => {
    // In a real app, you would fetch revenue data from your API
    // For now, we'll use the mock data
    const fetchRevenueData = async () => {
      try {
        // Simulate API call
        await new Promise((resolve) => setTimeout(resolve, 500));

        setRevenueData(mockRevenueData);
        setTopProducts(mockTopProducts);
      } catch (error) {
        console.error("Error fetching revenue data:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchRevenueData();
  }, []);

  // Calculate total revenue for the current period
  const totalRevenue =
    revenueData[period]?.reduce((sum, item) => sum + item.revenue, 0) || 0;

  // Calculate average order value (mock data)
  const averageOrderValue = 120.45;

  // Calculate conversion rate (mock data)
  const conversionRate = 3.24;

  // Calculate total customers (mock data)
  const totalCustomers = 842;

  // Simple bar chart component
  const BarChart = ({ data, period }) => {
    const maxRevenue = Math.max(...data.map((item) => item.revenue));

    return (
      <div className="w-full h-64 mt-4">
        <div className="flex items-end justify-between h-52 gap-2">
          {data.map((item, index) => (
            <div key={index} className="flex flex-col items-center flex-1">
              <div
                className="w-full bg-blue-500 rounded-t-md transition-all duration-500"
                style={{
                  height: `${(item.revenue / maxRevenue) * 100}%`,
                  minHeight: "10%",
                }}
              ></div>
              <span className="text-xs mt-2 text-muted-foreground">
                {item.date}
              </span>
            </div>
          ))}
        </div>
      </div>
    );
  };

  return (
    <div>
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Revenue</h1>
      </div>

      <Tabs defaultValue="overview" className="space-y-4">
        <TabsList>
          <TabsTrigger value="overview">Overview</TabsTrigger>
          <TabsTrigger value="analytics">Analytics</TabsTrigger>
          <TabsTrigger value="reports">Reports</TabsTrigger>
          <TabsTrigger value="notifications">Notifications</TabsTrigger>
        </TabsList>

        <TabsContent value="overview" className="space-y-4">
          <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">
                  Total Revenue
                </CardTitle>
                <DollarSign className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">
                  ${totalRevenue.toLocaleString()}
                </div>
                <p className="text-xs text-muted-foreground">
                  +20.1% from last month
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">
                  Average Order Value
                </CardTitle>
                <CreditCard className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">
                  ${averageOrderValue.toFixed(2)}
                </div>
                <p className="text-xs text-muted-foreground">
                  +2.5% from last month
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">
                  Conversion Rate
                </CardTitle>
                <TrendingUp className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{conversionRate}%</div>
                <p className="text-xs text-muted-foreground">
                  +0.8% from last month
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                <CardTitle className="text-sm font-medium">
                  Total Customers
                </CardTitle>
                <Users className="h-4 w-4 text-muted-foreground" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold">{totalCustomers}</div>
                <p className="text-xs text-muted-foreground">
                  +12.2% from last month
                </p>
              </CardContent>
            </Card>
          </div>

          <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-7">
            <Card className="col-span-4">
              <CardHeader>
                <div className="flex items-center justify-between">
                  <CardTitle>Revenue Over Time</CardTitle>
                  <div className="flex items-center space-x-2">
                    <button
                      onClick={() => setPeriod("daily")}
                      className={`px-2 py-1 text-xs rounded ${
                        period === "daily"
                          ? "bg-primary text-primary-foreground"
                          : "bg-muted"
                      }`}
                    >
                      Daily
                    </button>
                    <button
                      onClick={() => setPeriod("weekly")}
                      className={`px-2 py-1 text-xs rounded ${
                        period === "weekly"
                          ? "bg-primary text-primary-foreground"
                          : "bg-muted"
                      }`}
                    >
                      Weekly
                    </button>
                    <button
                      onClick={() => setPeriod("monthly")}
                      className={`px-2 py-1 text-xs rounded ${
                        period === "monthly"
                          ? "bg-primary text-primary-foreground"
                          : "bg-muted"
                      }`}
                    >
                      Monthly
                    </button>
                  </div>
                </div>
                <CardDescription>
                  Revenue trends over {period} periods
                </CardDescription>
              </CardHeader>
              <CardContent>
                {loading ? (
                  <div className="h-64 flex items-center justify-center">
                    <p>Loading chart data...</p>
                  </div>
                ) : (
                  <BarChart data={revenueData[period]} period={period} />
                )}
              </CardContent>
            </Card>

            <Card className="col-span-3">
              <CardHeader>
                <CardTitle>Top Selling Products</CardTitle>
                <CardDescription>
                  Products with the highest revenue this month
                </CardDescription>
              </CardHeader>
              <CardContent>
                {loading ? (
                  <div className="h-64 flex items-center justify-center">
                    <p>Loading product data...</p>
                  </div>
                ) : (
                  <div className="space-y-4">
                    {topProducts.map((product) => (
                      <div
                        key={product.id}
                        className="flex items-center justify-between"
                      >
                        <div className="flex items-center space-x-3">
                          <div className="h-8 w-8 rounded bg-muted flex items-center justify-center">
                            <Package className="h-4 w-4" />
                          </div>
                          <div>
                            <p className="text-sm font-medium">
                              {product.name}
                            </p>
                            <p className="text-xs text-muted-foreground">
                              {product.sales} sales
                            </p>
                          </div>
                        </div>
                        <p className="text-sm font-medium">
                          ${product.revenue.toFixed(2)}
                        </p>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        <TabsContent
          value="analytics"
          className="h-[400px] flex items-center justify-center border rounded-md"
        >
          <p className="text-muted-foreground">
            Detailed analytics will be available soon.
          </p>
        </TabsContent>

        <TabsContent
          value="reports"
          className="h-[400px] flex items-center justify-center border rounded-md"
        >
          <p className="text-muted-foreground">
            Report generation will be available soon.
          </p>
        </TabsContent>

        <TabsContent
          value="notifications"
          className="h-[400px] flex items-center justify-center border rounded-md"
        >
          <p className="text-muted-foreground">
            Notification settings will be available soon.
          </p>
        </TabsContent>
      </Tabs>
    </div>
  );
}
